import React, { useState } from "react";
import "./my_update.scss";
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function MyUpdateComponent({ user_name, user_email, user_hp, openJoinModal }) {

  const navigate = useNavigate();
  const [changeUserData, setChangeUserData] = useState({
    user_name: "",
    isNameError: false,
    isNameMsg: "",
    user_email: "",
    isEmailError: false,
    isEmailMsg: "",
    user_hp: "",
    isHpError: false,
    isHpMsg: "",
    user_pw: "",
    isPw1Error: false,
    isPw1Msg: "",
  });

  const onChangeUserName = (e) => {
    const { value } = e.target;
    let isNameError = false;
    let isNameMsg = "";
    const regExp = /[`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]/g;

    e.target.value = value.replace(regExp, "");
    if (value === "") {
      isNameError = true;
      isNameMsg = "  .";
    } else {
      isNameError = false;
      isNameMsg = "";
    }
    setChangeUserData({
      ...changeUserData,
      isNameError: isNameError,
      isNameMsg: isNameMsg,
      user_name: value,
    });
  };
  const onChangeUserEmail = (e) => {
    const { value } = e.target;
    let isEmailError = false;
    let isEmailMsg = "";
    const regExp =
      /^[a-z0-9---`~!#$%^&*\-_=+{}|'/?]+(\.)?[a-z0-9---`~!#$%^&*\-_=+{}|'/?]*@[a-z0-9---`~!#$%^&*\-_=+{}|'/\.?]+\.[a-z]{2,3}$/gi;

    if (value === "") {
      isEmailError = true;
      isEmailMsg = "  .";
      setChangeUserData({
        ...changeUserData,
        user_email: "",
        isEmailError: isEmailError,
        isEmailMsg: isEmailMsg,
      });
    } else if (regExp.test(value) === false) {
      isEmailError = true;
      isEmailMsg = "   .";
      setChangeUserData({
        ...changeUserData,
        user_email: "",
        isEmailError: isEmailError,
        isEmailMsg: isEmailMsg,
      });
    } else {
      axios({
        url: "/JSP/kream_getUserEmails.jsp",
        method: "GET",
      }).then((res) => {
        const result = res.data.map((item) => item === e.target.value);
        console.log(res);
        if (result.includes(true)) {
          isEmailError = true;
          isEmailMsg = " .";
          setChangeUserData({
            ...changeUserData,
            user_email: "",
            isEmailError: isEmailError,
            isEmailMsg: isEmailMsg,
          });
        } else {
          isEmailError = false;
          isEmailMsg = "";
          setChangeUserData({
            ...changeUserData,
            user_email: value,
            isEmailError: isEmailError,
            isEmailMsg: isEmailMsg,
          });
        }
      });
    }
  };
  const onChangeUserHp = (e) => {
    const { value } = e.target;
    const regExp1 = /[^0-9]/g;
    const regExp2 = /^01[0-9]{1}[0-9]{3,4}[0-9]{4}$/;
    let isHpError = false;
    let isHpMsg = "";

    //   
    e.target.value = value.replace(regExp1, "");

    if (value === "") {
      isHpError = true;
      isHpMsg = "   .";
      setChangeUserData({
        ...changeUserData,
        user_hp: "",
        isHpError: isHpError,
        isHpMsg: isHpMsg,
      });
    } else if (regExp2.test(value) === false) {
      isHpError = true;
      isHpMsg = "   .";
      setChangeUserData({
        ...changeUserData,
        user_hp: "",
        isHpError: isHpError,
        isHpMsg: isHpMsg,
      });
    } else {
      isHpError = false;
      isHpMsg = "";
      setChangeUserData({
        ...changeUserData,
        user_hp: value,
        isHpError: isHpError,
        isHpMsg: isHpMsg,
      });
    }
  };
  const onChangeUserPw1 = (e) => {
    const { value } = e.target;
    let isPw1Error = false;
    let isPw1Msg = "";

    const regExp1 = /^(.){10,16}$/g;
    const regExp2 =
      /([A-Za-z]+[0-9]+)+|([0-9]+[A-Za-z]+)+|([A-Za-z]+[`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]+)+|([`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]+[A-Za-z]+)+|([0-9]+[`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]+)+|([`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]+[0-9]+)+/g; // true 
    const regExp3 = /[---]/g;
    const regExp4 = /\s/g;
    const regExp5 = /(\d)\1\1/g;

    if (regExp1.test(value) === false) {
      isPw1Error = true;
      isPw1Msg = " 10  ";
      setChangeUserData({
        ...changeUserData,
        user_pw: "",
        isPw1Error: isPw1Error,
        isPw1Msg: isPw1Msg,
      });
    } else if (regExp3.test(value) === true) {
      isPw1Error = true;
      isPw1Msg = "  ";
      setChangeUserData({
        ...changeUserData,
        user_pw: "",
        isPw1Error: isPw1Error,
        isPw1Msg: isPw1Msg,
      });
    } else if (regExp2.test(value) === false || regExp4.test(value) === true) {
      isPw1Error = true;
      isPw1Msg = "//( ) , 2  ";
      setChangeUserData({
        ...changeUserData,
        user_pw: "",
        isPw1Error: isPw1Error,
        isPw1Msg: isPw1Msg,
      });
    } else if (regExp5.test(value) === true) {
      isPw1Error = true;
      isPw1Msg = "  3    ";
      setChangeUserData({
        ...changeUserData,
        user_pw: "",
        isPw1Error: isPw1Error,
        isPw1Msg: isPw1Msg,
      });
    } else {
      isPw1Error = false;
      isPw1Msg = "";
      setChangeUserData({
        ...changeUserData,
        user_pw: value,
        isPw1Error: isPw1Error,
        isPw1Msg: isPw1Msg,
      });
    }
  };
  const onClickSignUp = (e) => {
    e.preventDefault();
  
    if (changeUserData.user_pw === "") {
      openJoinModal(" ."); 
    } else if (changeUserData.user_name === "") {
      openJoinModal(" .");
    } else if (changeUserData.user_email === "") {
      openJoinModal(" .");
    } else if (changeUserData.user_hp === "") {
      openJoinModal("  .");
    } else {
      //  
      // 
      //  
      //  3  (1)  $1
      //  3~4(2)  $2
      //    4  (3)  $3
      const regExpHp = /^(\d{3})(\d{3,4})(\d{4})$/g; //010-7942-5305   010-348-6441

      //    


      //     

          let formData = new URLSearchParams();
          let user_id = sessionStorage.getItem("loginId");
          formData.append("user_id", user_id);
          formData.append("user_pw", changeUserData.user_pw);
          formData.append("user_name", changeUserData.user_name);
          formData.append("user_email", changeUserData.user_email);
          formData.append("user_hp", changeUserData.user_hp.replace(regExpHp, "$1-$2-$3"));
          
          axios({
            url: "/JSP/kream_setUserPwNameEmailHp.jsp",
            method: "POST",
            data: {},
            params: formData,
          })
          .then((res)=>{
            console.log(res.data);
            if(res.data===1){
              openJoinModal("  .");
              navigate("/my")
            } else {
              openJoinModal("  .");
            }
          })
          .catch((err)=>{
            console.log(err);
          })
        }
    } //else end   
  return (
    <div id="myUpdate">
      <div className="container">
        <div className="gap">
          <div className="title">
            <h3> </h3>
          </div>
          <div className="content">
            <ul>
              <li>
                <div className="img-box">
                  <img src="/img/blank_profile.png" alt="" />
                </div>
                <div className="text-box">
                  <div className="name-box">
                    <span>{user_name}</span>
                  </div>
                  <div className="btn-box">
                    <button> </button>
                    <button></button>
                  </div>
                </div>
              </li>
              <li>
                <form action="">
                  <ul>
                    <li>
                      <h4> </h4>
                    </li>
                    <li>
                      <label htmlFor="userName"> </label>
                      <input
                        type="text"
                        name="user_name"
                        id="userName"
                        placeholder={user_name}
                        autoComplete="off"
                        onChange={onChangeUserName}
                      />
                      <p
                        className={`error-msg${
                          changeUserData.isNameError ? " on" : ""
                        }`}
                      >
                        {changeUserData.isNameMsg}
                      </p>
                    </li>
                    <li>
                      <label htmlFor="userName"> </label>
                      <input
                        type="text"
                        name="user_name"
                        id="userName"
                        placeholder={user_email}
                        autoComplete="off"
                        onChange={onChangeUserEmail}
                      />
                      <p
                        className={`error-msg${
                          changeUserData.isEmailError ? " on" : ""
                        }`}
                      >
                        {changeUserData.isEmailMsg}
                      </p>
                    </li>
                    <li>
                      <label htmlFor="userName"></label>
                      <input
                        type="text"
                        name="user_name"
                        id="userName"
                        placeholder={user_hp}
                        autoComplete="off"
                        onChange={onChangeUserHp}
                      />
                      <p
                        className={`error-msg${
                          changeUserData.isHpError ? " on" : ""
                        }`}
                      >
                        {changeUserData.isHpMsg}
                      </p>
                    </li>
                    <li>
                      <label htmlFor="userName"></label>
                      <input
                        type="password"
                        name="user_name"
                        id="userName"
                        placeholder=""
                        onChange={onChangeUserPw1}
                      />
                      <p
                        className={`error-msg${
                          changeUserData.isPw1Error ? " on" : ""
                        }`}
                      >
                        {changeUserData.isPw1Msg}
                      </p>
                    </li>
                  </ul>
                  <button onClick={onClickSignUp} type="submit"></button>
                </form>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}
