import React from 'react';
import axios from 'axios';

import { Link } from 'react-router-dom';
import RankingComponetChild from './ranking/RankingComponetChild';

export default function RankingComponent() {
    
    const [state, setState] = React.useState({
        profile: [],
        post: [],
        current: 0,
        sortNum: 0
      });

    
      React.useEffect(()=>{
          axios({
              url: 'http://localhost:3000/data/style_page/found.json',
              method: 'GET'
          })
          .then((res)=>{
              if(res.status===200){
                const sorted = [...res.data.post].sort((a, b) => b.likes - a.likes);
                  setState({
                      ...state,
                      profile: res.data.profile,
                      post: sorted
                  })
              }
          })
          .catch((err)=>{
              console.log( `AXIOS  ! ${err}`);
          });
    
      },[]);

      const onClickRapidBtn = () => {
        setState({
            ...state,
            current : 0
        })
      }
      const onClickDailyBtn = () => {
        setState({
            ...state,
            current : 1
        })
      }
      const onClickWeeklyBtn = () => {
        setState({
            ...state,
            current : 2
        })
      }

      

  return (
    <section id="ranking">
        <div className="container">
            <div className="gap">
                <div className="title">
                    <h1>RANKING</h1>
                </div>
                <div className="content">
                    <div className="top-sticky__box">         
                            <nav className="tap-container">
                                <ul className="tap-inner">
                                    {/*  */}
                                    <li><Link to="/"><span></span></Link></li>
                                    {/*  */}
                                    <li><Link to="/style"><span></span></Link></li>
                                    {/*  */}
                                    <li><Link to="/style/ranking" className='on'><span></span></Link></li>
                                    {/*  */}
                                    <li><Link to="/style/sneakers"><span></span></Link></li>
                                    {/* ADYA */}
                                    <li><Link to="/style/adya"><span>ADYA</span></Link></li>
                                    {/*  */}
                                    <li><Link to="/style/luxury"><span></span></Link></li>
                                    {/*  */}
                                    <li><Link to="/style/clothing"><span></span></Link></li>
                                    {/*  */}
                                    <li><Link to="/style/bags"><span></span></Link></li>
                                    {/*  */}
                                    <li><Link to="/style/accessory"><span></span></Link></li>
                                    {/*  */}
                                    <li><Link to="/style/collectable"><span></span></Link></li>
                                </ul>
                            </nav>
                            <div className="filter-group__container">
                                <div className="filter-group__filter">
                                    <label>
                                        <input type="radio" className='blind' value=''/>
                                        <Link to='/style/ranking' className={ state.current === 0 ? 'filter-group__rapid-btn on': 'filter-group__rapid-btn' } onClick={onClickRapidBtn}></Link>
                                    </label>
                                    <label>
                                        <input type="radio" className='blind' value=''/>
                                        <Link to='/style/ranking' className={ state.current === 1 ? 'filter-group__rapid-btn on': 'filter-group__rapid-btn' } onClick={onClickDailyBtn}></Link>
                                    </label>
                                    <label>
                                        <input type="radio" className='blind' value=''/>
                                        <Link to='/style/ranking' className={ state.current === 2 ? 'filter-group__rapid-btn on': 'filter-group__rapid-btn' } onClick={onClickWeeklyBtn}></Link>
                                    </label>
                                </div>
                                <div className="filter-group__update-info">
                                    <span>UPDATE</span>
                                    <p>05.19 00:00</p>
                                    <Link to='/style/exhibitions'><i class="fa-regular fa-circle-question"></i></Link>
                                </div>
                            </div>
                        </div>
                    <div className="ranking-feed__container">
                        <RankingComponetChild post={state.post} />
                    </div>
                </div>
            </div>
        </div>
    </section>
  )
}
