import React from 'react';
import axios from 'axios';
// import react-router
import { Link } from 'react-router-dom';
// import component file
import FeedComponent from './FeedComponent';

export default function BagsComponent() {
  
  const [state, setState] = React.useState({
    profile: [],
    post: [],
    sorted: [],
    n: 0
  });

  React.useEffect(()=>{
      axios({
          url: 'http://localhost:3000/data/style_page/found.json',
          method: 'GET'
      })
      .then((res)=>{
          if(res.status===200){
              setState({
                  ...state,
                  profile: res.data.profile,
                  post: res.data.post,
                  n: res.data.length
              })
          }
      })
      .catch((err)=>{
          console.log( `AXIOS  ! ${err}`);
      });

  },[]);

  const recentSort = () => {
    const sortedImages = [...state.post].reverse();
    setState({
      ...state,
      sorted: sortedImages
    });
  }

  const popularSort = () => {
    const sorted = [...state.post].sort((a, b) => b.likes - a.likes);
    setState({
      ...state,
      sorted: sorted
    });
  }
  
  return (
      <section id="bags">
        <div className="container">
          <div className="gap">
            <div className="title">
              <h1>STYLE</h1>
            </div>
            <div className="content">
              <nav className="tap-container">
                <ul className="tap-inner">
                  {/*  */}
                  <li><Link to="/"><span></span></Link></li>
                  {/*  */}
                  <li><Link to="/style"><span></span></Link></li>
                  {/*  */}
                  <li><Link to="/style/ranking"><span></span></Link></li>
                  {/*  */}
                  <li><Link to="/style/sneakers"><span></span></Link></li>
                  {/* ADYA */}
                  <li><Link to="/style/adya"><span>ADYA</span></Link></li>
                  {/*  */}
                  <li><Link to="/style/luxury"><span></span></Link></li>
                  {/*  */}
                  <li><Link to="/style/clothing"><span></span></Link></li>
                  {/*  */}
                  <li><Link to="/style/bags" className='on'><span></span></Link></li>
                  {/*  */}
                  <li><Link to="/style/accessory"><span></span></Link></li>
                  {/*  */}
                  <li><Link to="/style/collectable"><span></span></Link></li>
                </ul>
              </nav>
              <div className="filter-group__container">
                  <div className="filter-group__filter">
                    <label>
                        <input type="radio" />
                        <Link to='/style/bags' className='filter-group__rapid-btn on'></Link>
                    </label>
                    <label>
                        <input type="radio" value=''/>
                        <Link to='/style/bags/minibag' className='filter-group__rapid-btn'></Link>
                    </label>
                    <label>
                        <input type="radio" value=''/>
                        <Link to='/style/bags/backpack' className='filter-group__rapid-btn'></Link>
                    </label>
                    <label>
                        <input type="radio" value=''/>
                        <Link to='/style/bags/celine' className='filter-group__rapid-btn'></Link>
                    </label>
                    <label>
                        <input type="radio" value=''/>
                        <Link to='/style/bags/crossbag' className='filter-group__rapid-btn'></Link>
                    </label>
                    <label>
                        <input type="radio" value=''/>
                        <Link to='/style/bags/channelbag' className='filter-group__rapid-btn'></Link>
                    </label>
                    <label>
                        <input type="radio" value=''/>
                        <Link to='/style/bags/ecobag' className='filter-group__rapid-btn'></Link>
                    </label>
                    <label>
                        <input type="radio" value=''/>
                        <Link to='/style/bags/cos' className='filter-group__rapid-btn'></Link>
                    </label>
                  </div>
              </div>
              <div className="sort-container">
                <ul className="sort-list">
                  <li><button onClick={popularSort}></button></li>
                  <li><button onClick={recentSort}></button></li>
                </ul>
              </div>
              <FeedComponent post={state.post} sorted= {state.sorted} />
            </div>
          </div>
        </div>
      </section>
  )
}
