import React, { useEffect, useRef, useState } from "react";
import "./join.scss";
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function JoinComponent({
  openAddrModal,
  user_addr,
  openFootModal,
  user_foot_size,
  openJoinModal
}) {
  const navigate = useNavigate();
  // 
  const [user, setUser] = useState({
    user_id: "",
    isIdError: false,
    isIdMsg: "",
    user_pw: "",
    isPw1Error: false,
    isPw1Msg: "",
    isPw2Error: false,
    isPw2Msg: "",
    user_name: "",
    isNameError: false,
    isNameMsg: "",
    user_email: "",
    isEmailError: false,
    isEmailMsg: "",
    user_hp: "01090579585",
    isHpError: false,
    isHpMsg: "",
    user_addr1: "",
    isUser_addr1: false,
    user_addr2: "",
    user_chocheon: "",
    user_foot_size: "",
    user_service: [
      "[]  14   .",
      " ",
      "    ",
      "[]     .",
      " ",
      " ",
      "",
    ],
    user_accepted: [],
  });


  //1 props  
  useEffect(() => {
    setUser({
      ...user,
      user_addr1: user_addr,
    });
  }, [user_addr]);
  //1     
  useEffect(() => {
    if (user.user_addr1 !== "") {
      setUser({
        ...user,
        isUser_addr1: true,
      });
    }
  }, [user.user_addr1]);
  //    
  useEffect(() => {
    setUser({
      ...user,
      user_foot_size: user_foot_size,
    });
  }, [user_foot_size]);

  //     
  const onChange = (e) => {
    const { value, name } = e.target;
    setUser({
      ...user,
      [name]: value,
    });
  };

  // 1.    
  const onChangeUserId = (e) => {
    const { value, name } = e.target;
    const regExp1 = /[`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]/g;
    const regExp2 = /^(.){6,16}$/g;
    const regExp3 = /[---]/g;
    const regExp4 = /[A-Za-z]+[0-9]*/g;
    const regExp5 = /\s/g;

    let isIdError = false;
    let isIdMsg = "";

    if (
      regExp1.test(value) === true ||
      regExp2.test(value) === false ||
      regExp3.test(value) === true ||
      regExp4.test(value) === false ||
      regExp5.test(value) === true
    ) {
      isIdError = true;
      isIdMsg = "6  16      ";

      setUser({
        ...user,
        user_id: "",
        isIdError: isIdError,
        isIdMsg: isIdMsg,
      });
    } else {
      let json = {
        username: value
      }
      axios.post('http://api.carlnathal.shop:8080/jwt/check', json).then((res) => {
        const result = res.data
        console.log(result)
        if (result) {
          isIdError = true;
          isIdMsg = " .";
          setUser({
            ...user,
            user_id: "",
            isIdError: isIdError,
            isIdMsg: isIdMsg,
          });
        } else {
          isIdError = false;
          isIdMsg = "";
          setUser({
            ...user,
            user_id: value,
            isIdError: isIdError,
            isIdMsg: isIdMsg,
          });
        }
      });
    }
  };

  // 2.    
  const onChangeUserPw1 = (e) => {
    const { value } = e.target;
    let isPw1Error = false;
    let isPw1Msg = "";

    const regExp1 = /^(.){10,16}$/g;
    const regExp2 =
      /([A-Za-z]+[0-9]+)+|([0-9]+[A-Za-z]+)+|([A-Za-z]+[`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]+)+|([`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]+[A-Za-z]+)+|([0-9]+[`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]+)+|([`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]+[0-9]+)+/g; // true 
    const regExp3 = /[---]/g;
    const regExp4 = /\s/g;
    const regExp5 = /(\d)\1\1/g;

    if (regExp1.test(value) === false) {
      isPw1Error = true;
      isPw1Msg = " 10  ";
      setUser({
        ...user,
        user_pw: "",
        isPw1Error: isPw1Error,
        isPw1Msg: isPw1Msg,
      });
    } else if (regExp3.test(value) === true) {
      isPw1Error = true;
      isPw1Msg = "  ";
      setUser({
        ...user,
        user_pw: "",
        isPw1Error: isPw1Error,
        isPw1Msg: isPw1Msg,
      });
    } else if (regExp2.test(value) === false || regExp4.test(value) === true) {
      isPw1Error = true;
      isPw1Msg = "//( ) , 2  ";
      setUser({
        ...user,
        user_pw: "",
        isPw1Error: isPw1Error,
        isPw1Msg: isPw1Msg,
      });
    } else if (regExp5.test(value) === true) {
      isPw1Error = true;
      isPw1Msg = "  3    ";
      setUser({
        ...user,
        user_pw: "",
        isPw1Error: isPw1Error,
        isPw1Msg: isPw1Msg,
      });
    } else {
      isPw1Error = false;
      isPw1Msg = "";
      setUser({
        ...user,
        isPw1Error: isPw1Error,
        isPw1Msg: isPw1Msg,
      });
    }
  };
  // 3.    
  const onChangeUserPw2 = (e) => {
    const { value } = e.target;
    const user_pw1 = document.getElementById("userPw").value;
    console.log(user_pw1);
    let isPw2Error = false;
    let isPw2Msg = "";
    if (value === "") {
      isPw2Error = true;
      isPw2Msg = "    .";
      setUser({
        ...user,
        isPw2Error: isPw2Error,
        isPw2Msg: isPw2Msg,
      });
    } else if (value !== user_pw1) {
      isPw2Error = true;
      isPw2Msg = "  ";
      setUser({
        ...user,
        isPw2Error: isPw2Error,
        isPw2Msg: isPw2Msg,
      });
    } else {
      isPw2Error = false;
      isPw2Msg = "";
      setUser({
        ...user,
        user_pw: user_pw1,
        isPw2Error: isPw2Error,
        isPw2Msg: isPw2Msg,
      });
    }
  };
  //4.    
  const onChangeUserName = (e) => {
    const { value } = e.target;
    let isNameError = false;
    let isNameMsg = "";
    const regExp = /[`~!@#$%^&*()\-_=+[{\]}\\|;:'",<.>/?]/g;

    e.target.value = value.replace(regExp, "");
    if (value === "") {
      isNameError = true;
      isNameMsg = "  .";
    } else {
      isNameError = false;
      isNameMsg = "";
    }
    setUser({
      ...user,
      isNameError: isNameError,
      isNameMsg: isNameMsg,
      user_name: value,
    });
  };
  //5.   
  const onChangeUserEmail = (e) => {
    const { value } = e.target;
    let isEmailError = false;
    let isEmailMsg = "";
    const regExp =
      /^[a-z0-9---`~!#$%^&*\-_=+{}|'/?]+(\.)?[a-z0-9---`~!#$%^&*\-_=+{}|'/?]*@[a-z0-9---`~!#$%^&*\-_=+{}|'/\.?]+\.[a-z]{2,3}$/gi;

    if (value === "") {
      isEmailError = true;
      isEmailMsg = "  .";
      setUser({
        ...user,
        user_email: "",
        isEmailError: isEmailError,
        isEmailMsg: isEmailMsg,
      });
    } else if (regExp.test(value) === false) {
      isEmailError = true;
      isEmailMsg = "   .";
      setUser({
        ...user,
        user_email: "",
        isEmailError: isEmailError,
        isEmailMsg: isEmailMsg,
      });
    } else {

      const json = {
        email: value
      }
      axios.post('http://api.carlnathal.shop:8080/jwt/email', json).then((res) => {
        if (res.data) {
          isEmailError = true;
          isEmailMsg = " .";
          setUser({
            ...user,
            user_email: "",
            isEmailError: isEmailError,
            isEmailMsg: isEmailMsg,
          });
        } else {
          isEmailError = false;
          isEmailMsg = "";
          setUser({
            ...user,
            user_email: value,
            isEmailError: isEmailError,
            isEmailMsg: isEmailMsg,
          });
        }
      });
    }
  };
  //6.   
  const onChangeUserHp = (e) => {
    const { value } = e.target;
    const regExp1 = /[^0-9]/g;
    const regExp2 = /^01[0-9]{1}[0-9]{3,4}[0-9]{4}$/;
    let isHpError = false;
    let isHpMsg = "";

    //   
    e.target.value = value.replace(regExp1, "");

    if (value === "") {
      isHpError = true;
      isHpMsg = "   .";
      setUser({
        ...user,
        user_hp: "",
        isHpError: isHpError,
        isHpMsg: isHpMsg,
      });
    } else if (regExp2.test(value) === false) {
      isHpError = true;
      isHpMsg = "   .";
      setUser({
        ...user,
        user_hp: "",
        isHpError: isHpError,
        isHpMsg: isHpMsg,
      });
    } else {
      isHpError = false;
      isHpMsg = "";
      setUser({
        ...user,
        user_hp: value,
        isHpError: isHpError,
        isHpMsg: isHpMsg,
      });
    }
  };
  //7.    
  const onClickUserAddr = (e) => {
    openAddrModal();
  };
  // 8.     
  const onChangeUserAddr1 = (e) => {
    setUser({
      ...user,
      user_addr1: e.target.value,
    });
  };
  // 9.    
  const onChangeUserAddr2 = (e) => {
    setUser({
      ...user,
      user_addr2: e.target.value,
    });
  };
  //10.    
  const onChangeUserChocheon = (e) => {
    setUser({
      ...user,
      user_chocheon: e.target.value,
    });
  };
  //     
  const onClickUserFootSize = (e) => {
    e.preventDefault();
    openFootModal();
  };
  //     
  const onChangeUserAccepted = (e) => {
    //    
    if (e.target.checked === true) {
      //   3  
      if (
        e.target.value === "[]  14   ." &&
        user.user_accepted.includes(" ") === false &&
        user.user_accepted.includes("    ") === false
      ) {
        setUser({
          ...user,
          user_accepted: [
            ...user.user_accepted,
            " ",
            "    ",
          ],
        });
      } else if (
        e.target.value === "[]  14   ." &&
        user.user_accepted.includes(" ") === true &&
        user.user_accepted.includes("    ") === false
      ) {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, "    "],
        });
      } else if (
        e.target.value === "[]  14   ." &&
        user.user_accepted.includes(" ") === false &&
        user.user_accepted.includes("    ") === true
      ) {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, " "],
        });
        //  
      } else if (e.target.value === " ") {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, " "],
        });
        //  
      } else if (e.target.value === "    ") {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, "    "],
        });
        //    
      } else if (
        e.target.value === "[]     ." &&
        user.user_accepted.includes(" ") === false &&
        user.user_accepted.includes(" ") === false &&
        user.user_accepted.includes("") === false
      ) {
        setUser({
          ...user,
          user_accepted: [
            ...user.user_accepted,
            " ",
            " ",
            "",
          ],
        });
      } else if (
        e.target.value === "[]     ." &&
        user.user_accepted.includes(" ") === true &&
        user.user_accepted.includes(" ") === false &&
        user.user_accepted.includes("") === false
      ) {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, " ", ""],
        });
      } else if (
        e.target.value === "[]     ." &&
        user.user_accepted.includes(" ") === false &&
        user.user_accepted.includes(" ") === true &&
        user.user_accepted.includes("") === false
      ) {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, " ", ""],
        });
      } else if (
        e.target.value === "[]     ." &&
        user.user_accepted.includes(" ") === false &&
        user.user_accepted.includes(" ") === false &&
        user.user_accepted.includes("") === true
      ) {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, " ", " "],
        });
      } else if (
        e.target.value === "[]     ." &&
        user.user_accepted.includes(" ") === true &&
        user.user_accepted.includes(" ") === true &&
        user.user_accepted.includes("") === false
      ) {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, ""],
        });
      } else if (
        e.target.value === "[]     ." &&
        user.user_accepted.includes(" ") === false &&
        user.user_accepted.includes(" ") === true &&
        user.user_accepted.includes("") === true
      ) {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, " "],
        });
      } else if (
        e.target.value === "[]     ." &&
        user.user_accepted.includes(" ") === true &&
        user.user_accepted.includes(" ") === false &&
        user.user_accepted.includes("") === true
      ) {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, " "],
        });
      } else if (e.target.value === " ") {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, " "],
        });
      } else if (e.target.value === " ") {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, " "],
        });
      } else if (e.target.value === "") {
        setUser({
          ...user,
          user_accepted: [...user.user_accepted, ""],
        });
      }

      //    
    } else {
      let user_accepted = [];
      if (e.target.value === "[]  14   .") {
        user_accepted = user.user_accepted.filter((item) => {
          return (
            item !== " " && item !== "    "
          );
        });
        setUser({
          ...user,
          user_accepted: user_accepted,
        });
      } else if (e.target.value === " ") {
        user_accepted = user.user_accepted.filter(
          (item) => item !== e.target.value
        );
        setUser({
          ...user,
          user_accepted: user_accepted,
        });
      } else if (e.target.value === "    ") {
        user_accepted = user.user_accepted.filter(
          (item) => item !== e.target.value
        );
        setUser({
          ...user,
          user_accepted: user_accepted,
        });
      } else if (
        e.target.value === "[]     ."
      ) {
        user_accepted = user.user_accepted.filter((item) => {
          return (
            item !== " " && item !== " " && item !== ""
          );
        });
        setUser({
          ...user,
          user_accepted: user_accepted,
        });
      } else if (e.target.value === " ") {
        user_accepted = user.user_accepted.filter(
          (item) => item !== e.target.value
        );
        setUser({
          ...user,
          user_accepted: user_accepted,
        });
      } else if (e.target.value === " ") {
        user_accepted = user.user_accepted.filter(
          (item) => item !== e.target.value
        );
        setUser({
          ...user,
          user_accepted: user_accepted,
        });
      } else if (e.target.value === "") {
        user_accepted = user.user_accepted.filter(
          (item) => item !== e.target.value
        );
        setUser({
          ...user,
          user_accepted: user_accepted,
        });
      }
    }
  };
  //    
  const onClickServiceShow = (e) => {
    e.preventDefault();
    e.target.classList.toggle("on");
    e.target.parentNode.nextElementSibling.classList.toggle("on");
  };
  useEffect(()=>{
    const regExpHp = /^(\d{3})(\d{3,4})(\d{4})$/g;
    console.log(user.user_hp.replace(regExpHp, "$1-$2-$3"));

  })
  //   
  const onClickSignUp = (e) => {
    e.preventDefault();
    let cnt = 0;
    user.user_accepted.map((item, idx) => {
      if (item.includes(" ")) {
        cnt++;
      } else if (item.includes("    ")) {
        cnt++;
      }
    });
    if (user.user_id === "") {
      openJoinModal(" .");
    } else if (user.user_pw === "") {
      openJoinModal(" .");
    } else if (user.user_name === "") {
      openJoinModal(" .");
    } else if (user.user_email === "") {
      openJoinModal(" .");
    } else if (user.user_hp === "") {
      openJoinModal("  .");
    } else if (user.user_addr1 === "") {
      openJoinModal(" .");
    } else if (user.user_addr2 === "") {
      openJoinModal(" .");
    } else if (user.user_hp === "") {
      openJoinModal("  .");
    } else if (cnt !== 2) {
      openJoinModal("  .");
    } else {
      //  
      // 
      //  
      //  3  (1)  $1
      //  3~4(2)  $2
      //    4  (3)  $3
      const regExpHp = /^(\d{3})(\d{3,4})(\d{4})$/g; //010-7942-5305   010-348-6441

      //    
      let user_accepted = "";
      user.user_accepted.map((item, idx) => {
        if (idx === user.user_accepted.length - 1) {
          user_accepted += item;
        } else {
          user_accepted += item + ", ";
        }
      });

      let user_my_chocheon_code = "";

      //   
      const generateRandomString = (length) => {
        const characters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
        let randomString = "";

        for (let i = 0; i < length; i++) {
          const randomIndex = Math.floor(Math.random() * characters.length);
          randomString += characters.charAt(randomIndex);
        }

        return randomString;
      };

      user_my_chocheon_code = generateRandomString(8);

      //     

      // let chocheonData = new URLSearchParams();
      // chocheonData.append("user_my_chocheon_code", user_my_chocheon_code);
      const json = {
        chocheon: user_my_chocheon_code
      }
      axios.post('http://api.carlnathal.shop:8080/jwt/chocheon', json)
        .then((res) => {
          if (res.data) {
            user_my_chocheon_code = generateRandomString(8);
          }
        })
        .then(() => {
          // let formData = new URLSearchParams();
          // formData.append("user_id", user.user_id);
          // formData.append("user_pw", user.user_pw);
          // formData.append("user_name", user.user_name);
          // formData.append("user_email", user.user_email);
          // formData.append("user_hp", user.user_hp.replace(regExpHp, "$1-$2-$3"));
          // formData.append("user_addr", `${user.user_addr1} ${user.user_addr2}`);
          // formData.append("user_chocheon", user.user_chocheon);
          // formData.append("user_foot_size", user.user_foot_size);
          // formData.append("user_accepted", user_accepted);
          // formData.append("user_my_chocheon_code", user_my_chocheon_code);
          let json = {
            username: user.user_id,
            password: user.user_pw,
            irum: user.user_name,
            email: user.user_email,
            hp: user.user_hp.replace(regExpHp, "$1-$2-$3"),
            addr: `${user.user_addr1} ${user.user_addr2}`,
            chocheon: user.user_chocheon,
            foot: user.user_foot_size,
            accepted: user_accepted,
            my: user_my_chocheon_code
          }
          
          axios.post('http://api.carlnathal.shop:8080/jwt/sign', json)
          .then((res)=>{
            console.log(res.data);
            openJoinModal(" .");
            navigate("/login")

          })
          .catch((err)=>{
            console.log(err);
          })
        })
        .catch((err) => {
          console.log(err);
        });
    } //else end   
  };

  return (
    <section id="join">
      <div className="container">
        <div className="gap">
          <div className="content">
            <div className="title">
              <h2></h2>
            </div>
            <form name="join_form" id="joinForm" action="" method="post">
              <ul>
                <li>
                  <label
                    htmlFor="userId"
                    className={`error-msg${user.isIdError ? " on" : ""}`}
                  >
                    <i>*</i>
                  </label>
                  <input
                    maxLength={16}
                    name="user_id"
                    id="userId"
                    type="text"
                    placeholder=" "
                    autoComplete="off"
                    onChange={onChangeUserId}
                  />
                  <p className={`error-msg${user.isIdError ? " on" : ""}`}>
                    {user.isIdMsg}
                  </p>
                </li>
                <li>
                  <label
                    htmlFor="userPw"
                    className={`error-msg${user.isPw1Error ? " on" : ""}`}
                  >
                    <i>*</i>
                  </label>
                  <input
                    name="user_pw"
                    id="userPw"
                    type="password"
                    placeholder=", ,   8-16"
                    autoComplete="off"
                    onChange={onChangeUserPw1}
                  />
                  <p className={`error-msg${user.isPw1Error ? " on" : ""}`}>
                    {user.isPw1Msg}
                  </p>
                </li>
                <li>
                  <label
                    htmlFor="userPw"
                    className={`error-msg${user.isPw2Error ? " on" : ""}`}
                  >
                     <i>*</i>
                  </label>
                  <input
                    name="user_pw2"
                    id="userPw2"
                    type="password"
                    placeholder="   ."
                    autoComplete="off"
                    onChange={onChangeUserPw2}
                  />
                  <p className={`error-msg${user.isPw2Error ? " on" : ""}`}>
                    {user.isPw2Msg}
                  </p>
                </li>
                <li>
                  <label
                    htmlFor="userName"
                    className={`error-msg${user.isNameError ? " on" : ""}`}
                  >
                    <i>*</i>
                  </label>
                  <input
                    name="user_name"
                    id="userName"
                    type="text"
                    placeholder=" "
                    autoComplete="off"
                    onChange={onChangeUserName}
                  />
                  <p className={`error-msg${user.isNameError ? " on" : ""}`}>
                    {user.isNameMsg}
                  </p>
                </li>
                <li>
                  <label
                    htmlFor="userEmail"
                    className={`error-msg${user.isEmailError ? " on" : ""}`}
                  >
                    <i>*</i>
                  </label>
                  <input
                    name="user_email"
                    id="userEmail"
                    type="text"
                    placeholder=")kream@kream.co.kr"
                    autoComplete="off"
                    onChange={onChangeUserEmail}
                  />
                  <p className={`error-msg${user.isEmailError ? " on" : ""}`}>
                    {user.isEmailMsg}
                  </p>
                </li>
                <li>
                  <label
                    htmlFor="userHp"
                    className={`error-msg${user.isHpError ? " on" : ""}`}
                  >
                    <i>*</i>
                  </label>
                  <input
                    name="user_hp"
                    id="userHp"
                    type="text"
                    placeholder=" "
                    autoComplete="off"
                    onChange={onChangeUserHp}
                  />
                  <p className={`error-msg${user.isHpError ? " on" : ""}`}>
                    {user.isHpMsg}
                  </p>
                </li>
                <li>
                  <label htmlFor="userAddr">
                    <i>*</i>
                  </label>
                  <input
                    name="user_addr"
                    id="userAddr"
                    type="button"
                    value={
                      user.user_addr1 === "" ? "" : user.user_addr1
                    }
                    className={`user-addr1${user.isUser_addr1 ? " off" : ""}`}
                    onClick={onClickUserAddr}
                    readOnly={user.isUser_addr1 ? true : false}
                    onChange={onChangeUserAddr1}
                  />
                  {user.isUser_addr1 && <span></span>}
                </li>
                <li>
                  <label htmlFor="userAddr">
                    <i>*</i>
                  </label>
                  <input
                    name="user_addr"
                    className="user-addr2"
                    id="userAddr2"
                    type="text"
                    autoComplete="off"
                    onChange={onChangeUserAddr2}
                  />
                </li>
                <li>
                  <label htmlFor="userChocheon"></label>
                  <input
                    name="user_chocheon"
                    id="userChocheon"
                    type="text"
                    placeholder="  "
                    autoComplete="off"
                    onChange={onChangeUserChocheon}
                    maxLength={6}
                  />
                </li>
                <li className="user-foot-li">
                  <label htmlFor="userFootSize"> </label>
                  <input
                    type="button"
                    name="user_foot_size"
                    id="userFootSize"
                    onChange={onChange}
                    onClick={onClickUserFootSize}
                    value={user.user_foot_size}
                  />
                </li>
                <li className="user-philsoo-li">
                  <div className="row1">
                    <input
                      type="checkbox"
                      name="user_philsoo_all"
                      id="userPhilsooAll"
                      onChange={onChangeUserAccepted}
                      value={user.user_service[0]}
                      checked={
                        user.user_accepted.includes(" ") &&
                        user.user_accepted.includes(
                          "    "
                        )
                      }
                    />
                    <label htmlFor="userPhilsooAll">
                      &#91;&#93;  14   .
                    </label>
                    <i onClick={onClickServiceShow}></i>
                  </div>
                  <div className="row2">
                    <div>
                      <input
                        type="checkbox"
                        name="user_philsoo_1"
                        id="userPhilsoo1"
                        checked={user.user_accepted.includes(" ")}
                        onChange={onChangeUserAccepted}
                        value={user.user_service[1]}
                      />
                      <label htmlFor="userPhilsoo1"></label>
                      <span> </span>
                    </div>
                    <div>
                      <input
                        type="checkbox"
                        name="user_philsoo_2"
                        id="userPhilsoo2"
                        checked={user.user_accepted.includes(
                          "    "
                        )}
                        onChange={onChangeUserAccepted}
                        value={user.user_service[2]}
                      />
                      <label htmlFor="userPhilsoo2">
                            
                      </label>
                      <span> </span>
                    </div>
                  </div>
                </li>
                <li className="user-ad-li">
                  <div className="row1">
                    <input
                      type="checkbox"
                      name="user_ad"
                      id="userAd"
                      value={user.user_service[3]}
                      onChange={onChangeUserAccepted}
                      checked={
                        user.user_accepted.includes(" ") &&
                        user.user_accepted.includes(" ") &&
                        user.user_accepted.includes("")
                      }
                    />
                    <label htmlFor="userAd">
                      &#91;&#93;     .
                    </label>
                    <i onClick={onClickServiceShow}></i>
                  </div>
                  <div className="row2">
                    <div>
                      <input
                        type="checkbox"
                        name="user_ad_1"
                        id="userAd1"
                        value={user.user_service[4]}
                        onChange={onChangeUserAccepted}
                        checked={user.user_accepted.includes(" ")}
                      />
                      <label htmlFor="userAd1"> </label>
                    </div>
                    <div>
                      <input
                        type="checkbox"
                        name="user_ad_2"
                        id="userAd2"
                        value={user.user_service[5]}
                        onChange={onChangeUserAccepted}
                        checked={user.user_accepted.includes(" ")}
                      />
                      <label htmlFor="userAd2"> </label>
                    </div>
                    <div>
                      <input
                        type="checkbox"
                        name="user_ad_3"
                        id="userAd3"
                        value={user.user_service[6]}
                        onChange={onChangeUserAccepted}
                        checked={user.user_accepted.includes("")}
                      />
                      <label htmlFor="userAd3"></label>
                    </div>
                  </div>
                </li>
                <li>
                  <button
                    type="submit"
                    className={`signup-btn ${
                      user.user_id !== "" &&
                      user.user_pw !== "" &&
                      user.user_name !== "" &&
                      user.user_email !== "" &&
                      user.user_hp !== "" &&
                      user.user_addr1 !== "" &&
                      user.user_addr2 !== "" &&
                      user.user_accepted.includes(" ") &&
                      user.user_accepted.includes("    ")
                        ? " on"
                        : ""
                    }`}
                    onClick={onClickSignUp}
                  >
                    
                  </button>
                </li>
              </ul>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
}
